import {
  Component,
  Input,
  ChangeDetectionStrategy,
} from '@angular/core';

import 'echarts/lib/chart/line';

import { BASE_CHART_PROVIDER } from '@covalent/echarts/base';

@Component({
  selector: 'td-chart-line',
  template: `<td-base-chart [style.height.%]="100"
                [data]="data">
  <ng-content></ng-content>
  <td-chart-x-axis [show]="true"
                    [position]="'bottom'"
                    [type]="'time'"
                    [boundaryGap]="false"
                    [axisLine]="{show: false}"
                    [splitLine]="{show: false}">
  </td-chart-x-axis>
  <td-chart-y-axis [show]="true"
                    [type]="'value'"
                    [axisLabel]="{inside: true}"
                    [axisLine]="{show: false}"
                    [splitLine]="{show: false}">
  </td-chart-y-axis>
</td-base-chart>`,
  styles: [`:host{display:block}`],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [BASE_CHART_PROVIDER],
})
export class TdLineChartComponent {

  @Input('data') data: any[];

}
